import jsPDF from "jspdf";
import "jspdf-autotable";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanPenjualanCustomer = (data) => {
  console.log(data);
  // initialize jsPDF
  const doc = new jsPDF("landscape");
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];

  doc.setFontSize(15);
  doc.text("LAPORAN PENJUALAN CUSTOMER", 14, 15);
  doc.setFontSize(10);
  //   //row 1
  //   doc.text(`Supplier : ${row1isi}`, 14, 25);
  //   //   row 2
  //   doc.text(`Tanggal	: ${row2isi}`, 120, 25);
  data.forEach((item, index) => {
    let tableColumn = [
      [
        {
          content: `TANGGAL : ${item.tanggal}`,
          colSpan: 2,
        },
        {
          content: `NO BON : ${item.no_bon}`,
          colSpan: 3,
        },
        {
          content: `NAMA TOKO : ${item.nama_toko}`,
          colSpan: 3,
        },
      ],
      [
        {
          content: `ONGKOS : ${item.ongkos_rp}`,
          colSpan: 2,
        },
        {
          content: `DISC GLOBAL : ${item.dics_global}`,
          colSpan: 2,
        },
        {
          content: `TOTAL NETTO : ${item.total_netto}`,
          colSpan: 2,
        },
        {
          content: `TOTAL NETTO EXT : ${item.total_netto_ext}`,
          colSpan: 2,
        },
      ],
      [
        {
          content: `KODE JENIS`,
        },
        {
          content: `NAMA JENIS`,
        },
        {
          content: `KADAR CETAK`,
        },
        {
          content: `KADAR JUAL`,
        },
        {
          content: `BRUTO`,
        },
        {
          content: `NETTO`,
        },
        {
          content: `DISC`,
        },
        {
          content: `NETTO DISC`,
        },
      ],
    ];
    item.detail_barang.forEach((hasil) => {
      let rows = [
        hasil.kode_jenis,
        hasil.nama_jenis,
        hasil.kadar_cetak,
        hasil.kadar_jual,
        hasil.bruto,
        hasil.netto,
        hasil.disc,
        hasil.netto_disc,
      ];
      tableRows.push(rows);
    });
    doc.autoTable({
      head: tableColumn,
      body: tableRows,
      startY: 30,
      theme: "plain",
      rowPageBreak: "avoid",
      pageBreak: "avoid",
      margin: { top: 20 },
      bodyStyles: { lineWidth: 0.02, lineColor: "#000" },
      headStyles: {
        lineWidth: 0.02,
        lineColor: "#000",
        fillColor: [212, 212, 211],
      },
    });
  });

  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  doc.autoPrint();
  // doc.save(`PenerimaanSUpplier.pdf`);
  var string = doc.output("datauristring");
  var embed = "<embed width='100%' height='100%' src='" + string + "'/>";
  var x = window.open();
  x.document.open();
  x.document.write(embed);
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanPenjualanCustomer;
