import Axios from "axios";
import React, { Component } from "react";
import { gagal } from "../Alert";
import { AxiosMasterGet } from "../Axios";
import FormCustomer from "./Component/FormCustomer";
import LaporanPembayaranCustomer from "./Component/LaporanPembayaranCustomer";
import LaporanPenjualanCustomer from "./Component/LaporanPenjualanCustomer";

const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class LaporanCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  handleGabung(data) {}
  handleHistory(data) {}
  handlePembayaran(data) {
    AxiosMasterGet(
      `report/penjualan/${data.kode_sales}&${data.tglFrom}&${data.tglTo}`
    )
      .then((res) => LaporanPenjualanCustomer(res.data))
      .catch((err) =>
        gagal("Gagal Mengambil data, Mohon periksa koneksi anda")
      );
  }
  handlePembayaranSales(data) {
    Axios.get(serverNonMaster + "cst/dc/pjl/details", {
      params: {
        tglFrom: data.tglFrom,
        tglTo: data.tglTo,
        kodeLokasiFrom: data.kodeLokasiFrom,
        kodelokasiTo: data.kodelokasiTo,
      },
    })
      .then((res) =>
        localStorage.setItem(
          "LaporanPembayaranSalesCustomer",
          JSON.stringify(res.data)
        )
      )
      .catch((err) =>
        gagal("Gagal Mengambil data, Mohon periksa koneksi anda")
      );
  }
  handleReturn(data) {
    Axios.get(serverNonMaster + "cst/dc/rtr/details", {
      params: {
        tglFrom: data.tglFrom,
        tglTo: data.tglTo,
        kodeLokasiFrom: data.kodeLokasiFrom,
        kodelokasiTo: data.kodelokasiTo,
      },
    })
      .then((res) =>
        localStorage.setItem("LaporanReturnCustomer", JSON.stringify(res.data))
      )
      .catch((err) =>
        gagal("Gagal Mengambil data, Mohon periksa koneksi anda")
      );
  }
  handleSaldoPiutang(data) {
    AxiosMasterGet(
      `report/bayar-piutang-customer/${data.kode_customer}&${data.tglFrom}&${data.tglTo}&REKAP&REKAP`
    )
      .then((res) => LaporanPembayaranCustomer(res.data))
      .catch((err) =>
        gagal("Gagal Mengambil data, Mohon periksa koneksi anda")
      );
  }
  handleTitipGr(data) {
    Axios.get(serverNonMaster + "cst/dc/ttp/details", {
      params: {
        tglFrom: data.tglFrom,
        tglTo: data.tglTo,
        kodeLokasiFrom: data.kodeLokasiFrom,
        kodelokasiTo: data.kodelokasiTo,
      },
    })
      .then((res) =>
        localStorage.setItem("LaporanTitipGrCustomer", JSON.stringify(res.data))
      )
      .catch((err) =>
        gagal("Gagal Mengambil data, Mohon periksa koneksi anda")
      );
  }
  handleTitipanPerCustomer(data) {}
  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12">
          <div className="card">
            <div className="card-body">
              <div className="col-lg-12">
                <ul className="nav nav-tabs nav-justified nav-bordered mb-3">
                  <li className="nav-item">
                    <a
                      href="#titipan-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link active"
                    >
                      <i className="mdi mdi-home-variant d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Titip Gr</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#pembayaran-tab"
                      data-toggle="tab"
                      aria-expanded="true"
                      className="nav-link "
                    >
                      <i className="mdi mdi-account-circle d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Penjualan</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#pembayaranSales-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">
                        Pembayaran Sales
                      </span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#return-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Return</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#history-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">history</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#gabung-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Gabung</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#saldoPiutang-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">
                        Saldo Piutang dan Titipan
                      </span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#titipanPerCustomer-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">
                        Titipan Per Customer
                      </span>
                    </a>
                  </li>
                </ul>
                <div className="tab-content">
                  <div className="tab-pane show active" id="titipan-tab">
                    <FormCustomer
                      from="titipan"
                      onSubmit={(data) => this.handleTitipGr(data)}
                    />
                  </div>
                  <div className="tab-pane" id="pembayaran-tab">
                    <FormCustomer
                      from="pembayaran"
                      onSubmit={(data) => this.handlePembayaran(data)}
                    />
                  </div>
                  <div className="tab-pane" id="pembayaranSales-tab">
                    <FormCustomer
                      from="pembayaranSales"
                      onSubmit={(data) => this.handlePembayaranSales(data)}
                    />
                  </div>
                  <div className="tab-pane" id="return-tab">
                    <FormCustomer
                      from="return"
                      onSubmit={(data) => this.handleReturn(data)}
                    />
                  </div>
                  <div className="tab-pane" id="history-tab">
                    <FormCustomer
                      from="history"
                      onSubmit={(data) => this.handleHistory(data)}
                    />
                  </div>
                  <div className="tab-pane" id="gabung-tab">
                    <FormCustomer
                      from="gabung"
                      onSubmit={(data) => this.handleGabung(data)}
                    />
                  </div>
                  <div className="tab-pane" id="saldoPiutang-tab">
                    <FormCustomer
                      from="saldoPiutang"
                      onSubmit={(data) => this.handleSaldoPiutang(data)}
                    />
                  </div>
                  <div className="tab-pane" id="titipanPerCustomer-tab">
                    <FormCustomer
                      from="titipanCustomer"
                      onSubmit={(data) => this.handleTitipanPerCustomer(data)}
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default LaporanCustomer;
